@import '../../../style/style.scss';

.card {
  @include center;
  @include column;
  @include rounded;
  @include reset-input;
  box-shadow: $shadow;
  background-color: $light;
}

.up {
  background-color: $light;
}

.down {
  color: $accent;
  background-color: $primaryDark;
  font-size: 2em;
  cursor: pointer;
}

.matched img {
  opacity: 0.3;
}
